.container {
    min-width: 500px;
    width: 100%;
}

.divider {
    margin-top: 10px;
    margin-bottom: 10px;
}

.inner-item {
    display: flex;
    flex-direction: row;
    width: 90%;
    text-align: left;
    font-size: 16px;
    margin-bottom: 10px;
    padding: 10px;
}

.item-left {
    width: 30%;
}

.item-right {
    width: 100%;
}

.text-row-right {
    padding-left: 15px;
}

.container-bottom {
    margin-top: 10px;
    margin-bottom: 20px;
}

.space {
    margin-left: 20px;
}

.inner-form {
    display: flex;
    flex-direction: row;
    width: 90%;
    text-align: left;
    font-size: 16px;
    padding-left: 10px;
}

.form-row {
    margin-top: 18px;
}

.button-row {
    margin-top: 6px;
}

.custom-form-field {
    width: 100%;
}

.form-radio {
    margin-top: 10px;
}

.alert-text {
    margin-bottom: 10px;
    color: blue;
}

.error-message{
    color: red;
}

.card-header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}